import config from 'next/config';
import React from 'react'
import { gsap } from "gsap"

import css from './index.scss'

const assetPrefix = config().publicRuntimeConfig.assetPrefix;

class Header extends React.Component {
  componentDidMount() {
    gsap.to(`.${css.logo}`, {y: 0, duration: 1});
  }
  render() {
    return (
      <div className={css.header}>
        <div className={css.logo}>
          <a href="#"><img src={`${assetPrefix}/logo.png`} alt="" /></a>
        </div>
        <div className={css.links}>
          <ul>
            <li>
              <span className={css.label}>menu</span>
              <span className={css.icon}></span>
            </li>
            <li>
              <span className={css.label}>let's talk</span>
              <span className={css.icon}></span>
            </li>
          </ul>
        </div>
      </div>
    )
  }
}

export default Header