import React from 'react'
import { connect } from 'react-redux'
import Head from 'next/head'

import Header from '../components/header'
import Intro from '../components/intro'

class Home extends React.Component {
  componentDidMount() {
    window.addEventListener('load', () => {
      this.props.APP_LOADED();
    });
    // const INTRO_LOADING = () => {
    //   window.setTimeout(() => {
    //     if(this.props.intro_loading < 100) {
    //       this.props.INTRO_LOADING(this.props.intro_loading + 1);
    //       INTRO_LOADING();
    //     }
    //   }, (4000 / 100));
    // }
    // INTRO_LOADING();
  }

  render() {
    return (
      <>
        <Head>
          <title>TRONX</title>
          <meta name="description" content="TRONX" />
        </Head>
        <div>
          <Header />
          <Intro />
        </div>
      </>
    )
  }
}

const mapStateToProps = state => ({
  app_loaded: state.app_loaded,
  intro_loading: state.intro_loading
});

const mapDispatchToProps = dispatch => ({
  APP_LOADED: () => {
    dispatch({type: 'APP_LOADED'})
  },
  INTRO_LOADING: (percentage) => {
    dispatch({type: 'INTRO_LOADING',payload: {percentage}})
  }
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Home)
